import axios, {
  AxiosError,
  AxiosRequestConfig,
  AxiosResponse,
  InternalAxiosRequestConfig,
  AxiosHeaders,
} from 'axios';
import Cookies from 'js-cookie';

// axios  
const axiosInstance = axios.create({
  baseURL: process.env.REACT_APP_API_URL || 'http://localhost:8080/api',
  timeout: 5000,
  withCredentials: true,
});

// accessToken   URL 
const excludeUrlEndings = ['/login', '/admin/reissue'];

//    accessToken  
axiosInstance.interceptors.request.use(
  (config: InternalAxiosRequestConfig): InternalAxiosRequestConfig => {
    //  accessToken 
    const accessToken = Cookies.get('accessToken'); //   .

    const isExcludedUrl = excludeUrlEndings.some(ending =>
      config.url?.endsWith(ending),
    );

    if (accessToken && !isExcludedUrl) {
      if (!config.headers) {
        config.headers = new AxiosHeaders();
      }
      config.headers.set('Authorization', `Bearer ${accessToken}`);
    }
    return config;
  },
  (error: AxiosError): Promise<AxiosError> => {
    return Promise.reject(error);
  },
);

//   
axiosInstance.interceptors.response.use(
  (response: AxiosResponse): AxiosResponse => {
    return response;
  },
  async (error: AxiosError): Promise<AxiosError> => {
    //  401  refresh token  accessToken 
    if (error.response?.status === 401) {
      try {
        // refresh token  accessToken 
        const response = await axiosInstance.get('/admin/reissue');
        const accessToken = response.headers['authorization'];
        if (accessToken) {
          Cookies.set('accessToken', accessToken, {
            expires: 1,
            secure: true,
            sameSite: 'Strict',
          });

          //    
          if (error.config) {
            error.config.headers.set('Authorization', `Bearer ${accessToken}`);
            return axiosInstance.request(error.config);
          }
        }
      } catch (refreshError) {
        console.error('Error refreshing token:', refreshError);
        // refresh token     
        Cookies.remove('accessToken');
        // window.location.href = '/login';
      }
    }
    return Promise.reject(error);
  },
);

export default axiosInstance;
