import { useState, useEffect } from 'react';
import axiosInstance from '../API/axiosInstance';

interface StatBoxProps {
  label: string;
  value: string | number;
  unit?: string;
}

interface DashboardStatsResponse {
  totalVisitors: number;
  dailyActiveUsers: number;
  dailyGamePlays: number;
  totalUsers: number;
  averageRating: number;
}

const useDashboardStats = () => {
  const [stats, setStats] = useState<StatBoxProps[]>([]);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchGeneralData = async () => {
      try {
        const res = await axiosInstance.get<DashboardStatsResponse>(
          '/admin/stat/dashboard',
        );
        const data = res.data;
        const statsData: StatBoxProps[] = [
          { label: '  ', value: data.totalVisitors, unit: '' },
          {
            label: '  ',
            value: data.dailyActiveUsers,
            unit: '',
          },
          {
            label: '   ',
            value: data.dailyGamePlays,
            unit: '',
          },
          { label: '  ', value: data.totalUsers, unit: '' },
          { label: ' ', value: data.averageRating, unit: '' },
        ];
        setStats(statsData);
      } catch (error) {
        console.error('Error fetching general data:', error);
        setError('    .');
      }
    };

    fetchGeneralData();
  }, []);

  return { stats, error };
};

export default useDashboardStats;
